*{
    margin:0;
    padding:0;
    font-family:sans-serif;
    box-sizing:border-box;
}
body{
    display:flex;
    height:auto;
    justify-content:center;
    align-items:center;
    padding:10px;
    background:linear-gradient(135deg, #71b7e6, #9b59b6);
}
.container{
    max-width:700px;
    width:100%;
    background:#fff;
    padding:25px 30px;
    border-radius:5px;
}
.container .title{
    font-size:25px;
    font-weight:500;
    position:relative;
}
.container .title::before{
    content: '';
    position:absolute;
    left:0;
    bottom:0;
    height:3px;
    width:30px;
    background:linear-gradient(135deg, #71b7e6, #9b59b6);
}
.container form .user-details{
    display:flex;
    flex-wrap:wrap;
    justify-content:space-between;
    margin:px 0 12px 0;
}
form .user-details .input-box{
    margin-bottom:15px;
    width:calc(100% / 2 - 20px);
}
.user-details .input-box .details{
    display:block;
    font-weight:500;
    margin-bottom:15px;
}
.user-details .input-box input{
    height:40px;
    width:100%;
    font-size:16px;
    border:1px solid #ccc;
    border-radius:5px;
    padding-left:10px;
    border-bottom-width: 2px;
    transition:all 0.3s ease;
}
.user-details .input-box:focus,
.user-details .input-box:valid{
    border-color:#9b59b6;
}
form .gender-details .gender-title{
    font-size:20px;
    font-weight:500;
}
.gender-details .category{
    display:flex;
    width:80%;
    margin:14px 0;
    justify-content:space-between;
}
.gender-details .category label{
    display:flex;
    align-items:center;
}
.gender-details .category .dot{
    height:18px;
    width:18px;
    border-radius:50%;
    background:#d9d9d9;
    margin-right:10px;
    border: 5px solid transparent;
    transition: all 0.3s ease;
}
#dot-1:checked ~ .category label .one,
#dot-2:checked ~ .category label .two,
#dot-3:checked ~ .category label .three{
    border-color:#d9d9d9;
    background: #9b59b6;
}
form input[type="radio"]{
    display:none;
}
form .button{
    height:45px;
    margin:45px 0;
}
form .button input{
    width:100%;
    height:100%;
    outline:none;
    color:#fff;
    border: none;
    font-size:18px;
    font-weight:500;
    border-radius:5px;
    letter-spacing:1px;
    background:linear-gradient(135deg, #71b7e6, #9b59b6);
}
form .button input:hover{
    background:(-135deg, #71b7e6, #9b59b6);
}
@media (max-width:584px){
    .container{
        max-width:100%;
    }
form .user-details .input-box{
    width:100%;
    margin-bottom:25px;
}
form .gender-details .category{
    width:100%;
}
.container form .user-details{
    max-height:300px;
    overflow-y:scroll;
}
.user-details::-webkit-scrollbar{
    width:0;
}
}